#root {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 1200px;
  margin: 0 auto;
}

.App {
  width: 800px;
}

.post {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  border: 3px solid #8b7e74;
  border-radius: 5px;
  margin-top: 15px;
  background-color: #eddbc7;
}

.post__content {
  font-size: 17px;
  line-height: 1.5;
}

.post-enter {
  -webkit-transform: translateX(-350px);
          transform: translateX(-350px);
}
.post-enter-active {
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
  transition: all 500ms ease-in;
}
.post-exit {
  opacity: 1;
}
.post-exit-active {
  -webkit-transform: translateX(-350px);
          transform: translateX(-350px);
  transition: all 500ms ease-in;
}

.post__btns {
  display: flex;
}
.post__btns button:not(:last-child) {
  margin-right: 6px;
}

.page__wrapper {
  margin-top: 30px;
  margin-bottom: 30px;
  display: flex;
  justify-content: center;
  grid-column-gap: 15px;
  -webkit-column-gap: 15px;
          column-gap: 15px;
}
.page {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border: 1px solid #753519;
  border-radius: 3px;
  cursor: pointer;
  font-size: 17px;
  font-weight: 600;
}

.page__current {
  background-color: #a9907e;
  color: white;
}

